}

.logo:hover .logo__icon {
  filter: drop-shadow(0 0 7px #ff001b);
  animation: none;
}

@keyframes pulsate {
  from {
    stroke: #fff;
    filter: drop-shadow(none);
    transform: scale3d(1, 1, 1);
  }

  50% {
    stroke: #ff6b01;
    filter: drop-shadow(0 0 2px #272e36);
    transform: scale3d(1.15, 1.15, 1.15);
  }

  to {
    stroke: #fff;
    filter: drop-shadow(none);
    transform: scale3d(1, 1, 1);
  }
}

.logo__icon {
  fill: rgba(0, 0, 0, 0);
  stroke: #fff;
  animation: pulsate 2.5s infinite alternate;
}

